import React, { useState, useCallback, useEffect } from "react";
import ImageGallery from "react-image-gallery";
import "react-image-gallery/styles/css/image-gallery.css";
import { useDropzone } from "react-dropzone";
import "./postAddPage.scss";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { useLocation } from 'react-router-dom';

const { kakao } = window;

const PostAddPage = () => {
  const useloca = useLocation();
  const params = useloca.state;
  console.log("****************", params)
  const [title, setTitle] = useState(params?.title || "");
  const [content, setContent] = useState(params?.content || "");
  const [rating, setRating] = useState(params?.rating || 4.0);
  const [location, setLocation] = useState(params?.location || "");
  const [galleryImages, setGalleryImages] = useState(
    params?.galleryImages || []
  );
  const navigate = useNavigate();

  const onDrop = useCallback((acceptedFiles) => {
    //   
    const imageFiles = acceptedFiles.filter((file) =>
      file.type.startsWith("image/")
    );

    //   galleryImages  
    const newImages = imageFiles.map((file) => ({
      //file  ?  S3   file ?
      file,
      original: URL.createObjectURL(file),
      thumbnail: URL.createObjectURL(file),
      originalHeight: 300,
      originalWidth: 400,
    }));
    setGalleryImages((prevGalleryImages) => [
      ...prevGalleryImages,
      ...newImages,
    ]);
  }, []);

  const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });

  const removeImage = (index) => {
    //   
    setGalleryImages((prevGalleryImages) =>
      prevGalleryImages.filter((img, i) => i !== index)
    );
  };

  const handleRatingChange = (e) => {
    const value = parseFloat(e.target.value);
    if (!isNaN(value) && value >= 0 && value <= 5) {
      setRating(value);
    }
  };

  const post = async (e) => {
    const config = {
      header: { "content-type": "multipart/form-data" },
    };
    e.preventDefault();
    const formData = new FormData();
    formData.append("title", title);
    formData.append("content", content);
    formData.append("rating", rating);
    formData.append("location", location);
    //   url .
    //   .
    //        .

    //  FormData 
    galleryImages.forEach((image, index) => {
      formData.append(`images`, image.file);
    });
    if (params?.id) {
      await axios.delete(`posts/${params.id}`);
    }
    await axios
      .post("posts", formData, config)
      .then(() => {
        alert(" .");
        navigate("/");
      })
      .catch((error) => {
        alert(error);
        alert(" .");
      });
  };

  useEffect(() => {
    kakao.maps.load(() => {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition((position) => {
          const latlng = new kakao.maps.LatLng(
            position.coords.latitude,
            position.coords.longitude
          );

          const options = {
            center: latlng,
            level: 3,
          };

          //  
          const map = new kakao.maps.Map(
            document.getElementById("map"),
            options
          );

          //    
          const geocoder = new kakao.maps.services.Geocoder();
          geocoder.coord2Address(
            latlng.getLng(),
            latlng.getLat(),
            (result, status) => {
              if (status === kakao.maps.services.Status.OK) {
                setLocation(result[0].address.address_name);
              }
            }
          );

          //     
          kakao.maps.event.addListener(map, "click", function (mouseEvent) {
            const clickedLatLng = mouseEvent.latLng;
            console.log("Clicked LatLng:", clickedLatLng);

            //    
            geocoder.coord2Address(
              clickedLatLng.getLng(),
              clickedLatLng.getLat(),
              (result, status) => {
                if (status === kakao.maps.services.Status.OK) {
                  setLocation(result[0].address.address_name);
                }
              }
            );

            //   
            map.setCenter(clickedLatLng);
          });
        });
      }
    }, []);
  }, []);

  return (
    <form encType="multipart/form-data" onSubmit={post}>
      <div className="PostPage">
        <div>
          <label>:</label>
          <input
            type="text"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            className="post-input"
          />
        </div>
        <div>
          <label>:</label>
          <textarea
            value={content}
            onChange={(e) => setContent(e.target.value)}
            rows="10"
            className="post-textarea"
          />
        </div>
        <div>
          <label>:</label>
          <input
            type="number"
            value={rating}
            onChange={handleRatingChange}
            step="0.5"
            min="0"
            max="5"
            className="post-input"
          />
        </div>
        <div id="map" style={{ width: "500px", height: "500px" }}></div>
        <div>
          <label>:</label>
          <input type="text" value={location} readOnly className="post-input" />
          <div id="map" style={{ width: "500px", hegiht: "500px" }}></div>
        </div>
        <div id="kakao-map" className="kakao-map"></div>
        <div>
          <h3>  </h3>
          <ImageGallery
            items={galleryImages}
            showPlayButton={false}
            showFullscreenButton={false}
            showNav={false}
            showThumbnails={true}
            renderItem={(item) => (
              <div className="image-item" width="400px" height="300px">
                <img
                  src={item.original}
                  alt={item.originalAlt}
                  className="gallery-image"
                  style={{
                    width: "400px",
                    height: "300px",
                    objectFit: "cover",
                    maxWidth: "100%",
                    maxHeight: "100%",
                  }}
                />
                <button
                  type="button"
                  onClick={() =>
                    removeImage(galleryImages.findIndex((img) => img === item))
                  }
                  className="delete-button"
                >
                  
                </button>
              </div>
            )}
          />
        </div>
        <div {...getRootProps()} className="dropzone">
          <input {...getInputProps()} accept="image/*" />
          {isDragActive ? (
            <p> !</p>
          ) : (
            <p>    .</p>
          )}
        </div>
        <button type="submit" className="post-button">
          
        </button>
      </div>
    </form>
  );
};

export default PostAddPage;
