import { useCallback, useEffect, useState } from "react";
import axios from "axios";
import { jwtDecode } from "jwt-decode"; //  : jwt-decode 
import MainTemplate from "./mainTemplate";
import MainTopBar from "./mainTopBar";
import MainTopNavigationBar from "./mainTopNavigationBar";
import PostList from "../postsPage/postList";
import { useCookies } from "react-cookie";

const MainPage = () => {
  const [cookies] = useCookies(["authorization"]);
  let userId = 0;
  useEffect(() => {
    //  JWT  .
    const token = cookies.authorization;

    if (token) {
      // JWT  .
      const decodedToken = jwtDecode(token);
      userId = decodedToken.id;
    }
  }, [cookies.authorization]);

  const [data, setData] = useState([]);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  //    
  const search = useCallback((id) => {
    axios
      .get(`posts/${id}`)
      .then((response) => {
        setData(
          response.data.post.map((data) => {
            //   
            let galleryImages = [];
            if (data.imagePath) {
              galleryImages = data.imagePath.split(",").map((image) => {
                return {
                  original: image,
                  originalAlt: "  .",
                  thumbnail: image,
                  thumbnailAlt: "  .",
                };
              });
            }
            return {
              userId,
              id: data.id,
              author: data.author,
              title: data.title,
              galleryImages: galleryImages,
              content: data.content,
              rating: data.rating,
              location: data.location,
              comments: [" 1-1", " 1-2", " 1-3"],
              date: data.createdAt,
            };
          })
        );
      })
      .catch((error) => {
        setData([]);
      });
  }, []);

  useEffect(() => {
    axios
      .get("posts")
      .then((response) => {
        setData(
          response.data.post.map((data) => {
            //   
            let galleryImages = [];
            if (data.imagePath) {
              galleryImages = data.imagePath.split(",").map((image) => {
                return {
                  original: image,
                  originalAlt: "  .",
                  thumbnail: image,
                  thumbnailAlt: "  .",
                };
              });
            }
            return {
              userId,
              id: data.id,
              author: data.author,
              title: data.title,
              galleryImages: galleryImages,
              content: data.content,
              rating: data.rating,
              location: data.location,
              comments: [" 1-1", " 1-2", " 1-3"],
              date: data.createdAt,
            };
          })
        );
      })
      .catch((error) => {
        setData([]);
      });
  }, []);

  useEffect(() => {
    //     .
    const authorizationCookie = document.cookie
      .split("; ")
      .find((row) => row.startsWith("authorization="));

    if (authorizationCookie) {
      //        
      const token = authorizationCookie.split("=")[1];
      const decodedToken = jwtDecode(token);

      //      
      if (decodedToken.exp * 1000 > Date.now()) {
        //     
        setIsLoggedIn(true);
      } else {
        //    
        setIsLoggedIn(false);
      }
    } else {
      //     
      setIsLoggedIn(false);
    }
  }, []);

  return (
    <div>
      <MainTemplate>
        <MainTopBar>
          <MainTopNavigationBar
            search={search}
            isLoggedIn={isLoggedIn}
            setIsLoggedIn={setIsLoggedIn}
          />
        </MainTopBar>
      </MainTemplate>
      <PostList posts={data} />
      {data.length === 0 && <div>  .</div>}
    </div>
  );
};

export default MainPage;
